@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body{font-family:"Roboto", sans-serif !important;color:#fff !important}.vertical-timeline::before{background:black !important}.ppc-background{background-color:#000;background-image:linear-gradient(to top, black 0%, darkgrey 100%)}.ppc-background__timeLine{background-color:#000}.ppc-hero{height:100vh}.ppc-tombstone{animation:shake 0.8s}.ppc-tombstone:hover{animation:shake 0.8s infinite}@keyframes shake{0%{transform:translate(1px, 1px) rotate(0deg)}10%{transform:translate(-1px, -2px) rotate(-1deg)}20%{transform:translate(-3px, 0px) rotate(1deg)}30%{transform:translate(3px, 2px) rotate(0deg)}40%{transform:translate(1px, -1px) rotate(1deg)}50%{transform:translate(-1px, 2px) rotate(-1deg)}60%{transform:translate(-3px, 1px) rotate(0deg)}70%{transform:translate(3px, 1px) rotate(-1deg)}80%{transform:translate(-1px, -1px) rotate(1deg)}90%{transform:translate(1px, 2px) rotate(0deg)}100%{transform:translate(1px, -2px) rotate(-1deg)}}

